import colours from './colours';

var sharedStyles = {};

sharedStyles.sectionHeading = {
  fontFamily: 'Apercu-Bold',
  textTransform: 'uppercase',
  color: colours.blue,
  fontSize: '4.5vh',
  letterSpacing: '0.06em',
  textAlign: 'center'
}

export default sharedStyles;


